
export default {
  name: 'reg-form',
  components: {},
  props: [],
  data () {
    return {
      login:"",
      password:"",
      passwordConfirmed:"",
      isError:false,
      messageError:""
    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {
    validateForm: function(){
      if(!/^(?=.*[A-Za-z0-9]$)[A-Za-z][A-Za-z\d.-]{0,19}$/.test(this.login)){
        this.messageError = "Введите нормальный логин";
        this.isError = true;
        return;
      }
      if(this.password!==this.passwordConfirmed){
        this.messageError = "Пароль не совпадает";
        this.isError = true;
        return;
      }
      this.messageError="";

    }
  }
}


